/* src/pages/Contact.css */
.contact-container {
  max-width: 1080px;
  margin: 0 auto;
  padding: 80px 24px 96px;
  font-family: 'Poppins', sans-serif;
  position: relative;
}

.contact-container::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(160deg, rgba(255, 255, 255, 0.95), rgba(250, 240, 231, 0.9));
  border-radius: 28px;
  border: 1px solid rgba(201, 123, 99, 0.1);
  box-shadow: 0 32px 60px rgba(149, 118, 97, 0.18);
  z-index: 0;
}

.contact-container > * {
  position: relative;
  z-index: 1;
}

.page-title {
  font-size: clamp(2.4rem, 4vw, 3rem);
  text-align: center;
  margin: 0 0 28px;
  color: var(--accent);
}

.contact-intro {
  max-width: 640px;
  margin: 0 auto 40px;
  text-align: center;
  color: var(--text-muted);
  line-height: 1.7;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
}

.contact-card {
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(201, 123, 99, 0.14);
  border-radius: 24px;
  padding: clamp(24px, 4vw, 32px);
  box-shadow: 0 24px 44px rgba(149, 118, 97, 0.16);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-card h2 {
  margin: 0;
  font-size: clamp(1.6rem, 3vw, 2rem);
  color: var(--text-primary);
}

.contact-card--form .appointment-container {
  width: 100%;
}

.contact-card--coverage .coverage-list {
  display: grid;
  gap: 18px;
}

.coverage-list article h3 {
  margin: 0 0 10px;
  font-size: 1.05rem;
  color: var(--accent);
}

.coverage-list ul {
  margin: 0;
  padding-left: 18px;
  color: var(--text-muted);
  line-height: 1.7;
}

@media (max-width: 768px) {
  .contact-container {
    padding: 64px 16px 88px;
  }
}
